.presets {
  width: calc(99.9% * 3.6/15 - (30px - 30px * 3.6/15));
  text-align:center;
  margin: 0px 0px 0 15px !important;
  position:relative;
}.presets:nth-child(1n) {
  float: left;
  margin-right: 30px;
  clear: none;
}.presets:last-child {
  margin-right: 0;
}.presets:nth-child(15n) {
  margin-right: 0;
  float: right;
}.presets:nth-child(15n + 1) {
  clear: both;
}

.presets .save {
  float: left;
  background-image: url('../../images/plus_icon.svg');
}

.presets .download {
  background-image: url('../../images/darro_icon.svg');
}

.presets .upload {
  background-image: url('../../images/uarro_icon.svg');
}

.presets .new-preset-name {
  width: calc(99.9% * 12/12 - (30px - 30px * 12/12));
  height: 65px;
  text-align: center;
  position: absolute;
  top: 0px;
  left: 0px;
  background-color: var(--bg-color);
  background-image: -webkit-gradient(linear,left top, left bottom,from(#191919),color-stop(220%, #242424));
  background-image: -webkit-linear-gradient(#191919 0%,#242424 220%);
  background-image: -o-linear-gradient(#191919 0%,#242424 220%);
  background-image: linear-gradient(#191919 0%,#242424 220%);
  border-radius: 3px;
  -webkit-transform: translateY(-150px);
      -ms-transform: translateY(-150px);
          transform: translateY(-150px);
  -webkit-transition: all 100ms ease-out;
  -o-transition: all 100ms ease-out;
  transition: all 100ms ease-out;
}

.presets .new-preset-name:nth-child(1n) {
  float: left;
  margin-right: 30px;
  clear: none;
}

.presets .new-preset-name:last-child {
  margin-right: 0;
}

.presets .new-preset-name:nth-child(12n) {
  margin-right: 0;
  float: right;
}

.presets .new-preset-name:nth-child(12n + 1) {
  clear: both;
}

.presets .new-preset-name.visible{
  -webkit-transform: translateY(0px);
      -ms-transform: translateY(0px);
          transform: translateY(0px);
  -webkit-transition: all 100ms ease-out;
  -o-transition: all 100ms ease-out;
  transition: all 100ms ease-out;
}

.presets .new-preset-name input{
  display: block;
  width: 90%;
  margin: 0 auto 4px auto;
  padding: 4px 3px;
  border: 1px solid var(--gray);
}

.toolbar .presets .new-preset-name button{
  width: auto;
  height: auto;
  padding: 0.85em;
  font-weight: bold;
}


