.keys {
    position: fixed;
    left: 0;
    width:100%;
    font-family: Sans-Serif;
    font-weight: 100;
    border-radius: 3px;
    padding: 0 0 2em 0;
    clear:both;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    bottom: 0px;
    -webkit-transform:translateY(0px);
        -ms-transform:translateY(0px);
            transform:translateY(0px);
    background-color: rgba(0, 0, 0, 0.85);
    border-top: 2px solid rgb(0, 0, 0);
    -webkit-transition: all 130ms ease-out;
    -o-transition: all 130ms ease-out;
    transition: all 130ms ease-out;
    Z-index: 100;
}

.keys label{
  margin-bottom: 0.5em;
}

.keys .keyboard {
  display: block;
  width: 1000px;
  margin: 1em auto;
}

.keys.disabled{
  -webkit-transform:translateY(342px);
      -ms-transform:translateY(342px);
          transform:translateY(342px);
  overflow: hidden
}

.keys .keyboard:after,
.keys:after {
    display: table;
    content: "";
    clear: both;
}

.keys .keyboard > div {
    display: block;
    float: left;
    width: 40px;
    height: 140px;
    margin: 20px 0px;
    padding: 0 4px;
    text-align: center;
    border: 1px solid var(--light-gray);
    line-height: 240px;
    border-radius: 3px;
    background-color: var(--white);
    color: var(--bg-color);
    cursor: pointer;
    font-size: 1em;
}

.keys .keyboard > div[class*='♭']:hover,
.keys .keyboard > div:hover {
  background-color: var(--gray);
  color: var(--white);
}

.keys .keyboard > div[class*='♭'] {
    background: var(--light-gray);
    margin-top: 10px;
    color: var(--white);
}

.keys .keyboard > div[class*='♭']:active,
.keys .keyboard > div:active {
  background-color: var(--bg-color);
}

.keys .key sub {
    font-size: 0.7em;
    margin: 0.15em;
}

.octave{
  display: block;
  width: 275px;
  margin: 1em auto 0 auto;
  text-align: center;
}

.octave:before{
  content: '';
  display: table;
}

.octave:after{
  content: '';
  display: table;
  clear: both;
}

.octave input {
  display: inline-block;
  width: 80%;
  margin-top: 0.5em;
}

.toggle-keyboard {
  background-image: url('../../images/piano_icon.svg');
}

.keys .toggle-keyboard{
  position: absolute;
  background-image: none;
  display: block;
  top: 6px;
  right: 6px;
  font-size: 1em;
  margin: 0;
  padding: 0 0 2.25px 0;
  line-height: 0;
}

.keys .toggle-keyboard[data-title]:after {
  left: -50%;
  line-height: 1em;
}

.toggle-over {
  background-image: url('../../images/mouse_icon.svg');
  -webkit-transform: translateY(-25%);
      -ms-transform: translateY(-25%);
          transform: translateY(-25%);
}


