.midi {
  width:calc(99.9% * 4/15 - (30px - 30px * 4/15));
  text-align:center;
  position: relative;
  margin: 0px 5px 0px 10px !important;
}.midi:nth-child(1n) {
  float:left;
  margin-right:30px;
  clear:none;
}.midi:last-child {
  margin-right:0;
}.midi:nth-child(15n) {
  margin-right:0;
  float:right;
}.midi:nth-child(15n + 1) {
  clear:both;
}

.refresh-midi {
  float: left;
  background-image: url('../../images/refresh_icon.svg');
}


