.master-volume{
  width: calc(99.9% * 2.5/15 - (30px - 30px * 2.5/15));
  margin: 0 0 0 0 !important;
  text-align: center;
}.master-volume:nth-child(1n){
  float: left;
  margin-right: 30px;
  clear: none;
}.master-volume:last-child{
  margin-right: 0;
}.master-volume:nth-child(15n){
  margin-right: 0;
  float: right;
}.master-volume:nth-child(15n + 1){
  clear: both;
}

.master-volume .volume {
  display: block;
  float: left;
  width: 76.5%;
  margin: 0.5em 0 0 -1.5%;
}

.master-volume button {
  background-image: url('../../images/speaker_icon.svg');
  display: inline-block;
  float: left;
}


