.pitch-control {
  margin: 2em 0 0 0.25em;
}

.pitch-control h2 {
  margin-top: 0;
}

.pitchOffset {
  display:block;
  float:left;
  clear:both;
  background: #000;
  margin: 0;
  padding: 0.5em 0.5em;
  width: 100%;
  text-align: center;
  color: var(--red);
  border-width: 1px 2px;
  border-color: var(--gray);
  border-style:solid;
  line-height: 1em;
  font-size: 14px;
  letter-spacing: normal;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

.pitch-control button{
  display: block;
  float: left;
  clear:both;
  width: 100%;
  margin: 0;
  padding: 0;
  height: 29px;
  background-repeat: no-repeat;
  background-position: 50% 50%;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  border-color: var(--gray);
}

.pitch-control button.up {
  background-image: url('../../images/uarro_icon.svg');
  border-radius: 3px 3px 0 0 !important;
  border-width: 2px 2px 0 2px !important;
}

.pitch-control button.down {
  background-image: url('../../images/darro_icon.svg');
  border-radius: 0 0 3px 3px !important;
  border-width: 0 2px 2px 2px !important;
}

.pitch-control button.down[data-title]:after {
  top: 100%;
  bottom: auto;
}


