.caffeine {
  width: calc(99.9% * 0.9/15 - (30px - 30px * 0.9/15));
  margin: 0 !important;
  line-height: 20px;
  text-align: center;
}.caffeine:nth-child(1n) {
  float: left;
  margin-right: 30px;
  clear: none;
}.caffeine:last-child {
  margin-right: 0;
}.caffeine:nth-child(15n) {
  margin-right: 0;
  float: right;
}.caffeine:nth-child(15n + 1) {
  clear: both;
}

.caffeine:after {
  content: '';
  display: table;
  clear: both;
}

.caffeine button{
  display: inline;
  margin: 0 0 0 0;
  background-image: url('../../images/coffee_icon.svg');
  background-size: 70%;
  background-position: 50.6% 50%;
  background-repeat: no-repeat;
}

.caffeine button[data-title]:after {
  left: -50%;
  line-height: 1em;
}


