:root {
  --white: #cecece;
  --translucent-white: rgba(216,216,216, 0.35);
  --bg-color: #191919;
  --translucent-bg-color: rgba(25,25,25, 0.96);
  --gray: #444;
  --light-gray: #666;
  --red: #B22C2C;
  --light-red: #c33d3d;
}

@font-face {
  font-family: "PT Sans";
  src: url("./fonts/PTS55F-webfont.eot");
  src: url("./fonts/PTS55F-webfont.eot?#iefix") format("embedded-opentype"),
  url("./fonts/PTS55F-webfont.woff") format("woff"),
  url("./fonts/PTS55F-webfont.ttf") format("truetype"),
  url("./fonts/PTS55F-webfont.svg#pt_sansregular") format("svg");
  font-weight: normal;
  font-style: normal;
}

@-webkit-keyframes fade-in{
  0% {
    opacity: 0;
    width: auto;
    height: auto;
    visibility: visible;
  }
  100% {
    opacity: 1;
    width: auto;
    height: auto;
    visibility: visible;
  }
}

@keyframes fade-in{
  0% {
    opacity: 0;
    width: auto;
    height: auto;
    visibility: visible;
  }
  100% {
    opacity: 1;
    width: auto;
    height: auto;
    visibility: visible;
  }
}

.app {
  max-width: calc(99.9% * 12/12);
  margin-left: auto;
  margin-right: auto;
  padding-left: 0;
  padding-right: 0;
  min-width: 1024px;
  max-width: 1600px;
  margin-top: 40px;
}

.app:before {
  content: '';
  display: table;
}

.app:after {
  content: '';
  display: table;
  clear: both;
}

.titlebar {
  display: none;
  width: 100%;
  height: 26px;
  background-color: rgba(0, 0, 0, 0.85);
  -webkit-box-shadow: 0px 0px 4px 0 var(--bg-color);
          box-shadow: 0px 0px 4px 0 var(--bg-color);
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1000;
  text-align: center;
  line-height: 26px;
  font-size: 14px;
  font-weight: light;
  -webkit-app-region: drag;
}

.titlebar.win32,
.titlebar.darwin{
  display: block;
}

.titlebar.darwin #window-controls{
  display: none;
}

#window-controls {
  position: absolute;
  top: -4px;
  right: 0px;
  width: 125px;
  height: 24px;
  text-align: right;
  padding-right: 0.15em;
  font-size: 1.8em;
  -webkit-app-region: no-drag;
  border-radius: 3px 3px 0 0;
}

#window-controls #min-btn,
#window-controls #max-btn,
#window-controls #close-btn {
  display: inline-block;
  width: 24px;
  height: 24px;
  margin: 0;
  padding: 0;
  text-align: center;
  position: relative;
}

#window-controls #max-btn {
  font-size: 1.15em;
}

#window-controls #min-btn {
  -webkit-transform: translateY(-5px);
      -ms-transform: translateY(-5px);
          transform: translateY(-5px);
}

#window-controls #close-btn {
  -webkit-transform: translateY(3px);
      -ms-transform: translateY(3px);
          transform: translateY(3px);
  font-size: 1.2em;
}

.shaping {
  width: calc(99.9% * 12/12 - (30px - 30px * 12/12));
  margin-bottom: 2em;
}

.shaping:nth-child(1n) {
  float: left;
  margin-right: 30px;
  clear: none;
}

.shaping:last-child {
  margin-right: 0;
}

.shaping:nth-child(12n) {
  margin-right: 0;
  float: right;
}

.shaping:nth-child(12n + 1) {
  clear: both;
}

.shaping:before {
  content: '';
  display: table;
}

.shaping:after {
  content: '';
  display: table;
  clear: both;
}

html {
  display: block;
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
  overflow: hidden;
  position: relative;
  border-radius: 3px 3px 0 0;
  background-color:transparent;
}

body {
  position: absolute;
  left: 0;
  display: block;
  margin: 0;
  padding: 0;
  background-color: var(--bg-color);
  background-image: -webkit-radial-gradient(#242424 0%, #191919 90%);
  background-image: -o-radial-gradient(#242424 0%, #191919 90%);
  background-image: radial-gradient(#242424 0%, #191919 90%);
  color: var(--white);
  font-family: "PT Sans";
  letter-spacing: 0.03em;
  width: 100%;
  height: 100%;
  overflow-y: scroll;
  overflow-x: hidden;
  -webkit-overflow-scrolling: touch;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  cursor: default;
  font-size: 1em !important;
  border-radius: 3px 3px 0 0;
}

body:hover::-webkit-scrollbar {
  opacity: 1;
}

body::-webkit-scrollbar {
  width: 5px;
  background: none;
  opacity: 0;
  -webkit-transition: all 130ms linear;
  -o-transition: all 130ms linear;
  transition: all 130ms linear;
}

body::-webkit-scrollbar-thumb {
  background: var(--gray);
}

canvas {
  -webkit-transform: translateZ(0);
          transform: translateZ(0);
}

label {
  font-size: 0.8em;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  cursor: default;
  padding: 0;
  display: block;
  clear: both;
  margin: 1em 0 0.25em;
}

select {
  display: block;
  float: left;
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
  background: url(data:image/svg+xml;base64,);
  background-repeat: no-repeat;
  background-position: right 8px center;
  background-color: var(--gray);
  color: var(--white);
  cursor: pointer;
  border-radius: 3px;
  width: 45%;
  height: 27px;
  margin: 4px 4px 0 5%;
  padding: 0 30px 0 8px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  outline: none;
  border: 0px;
}

optgroup {
  outline: none;
  border: 0px;
}

select:focus,
select:hover {
  background-color: var(--gray);
  border: 1px solid var(--translucent-white);
  border-radius: 2px;
}

option:hover {
  background-color: var(--red) !important;
}

option:checked {
  background-color: var(--bg-color);
}

h2 {
  font-size: 1em;
}

input[type=range] {
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
  background: none;
  cursor:pointer;
}

input[type=range]:focus {
  outline: none;
}

input[type=range]::-webkit-slider-runnable-track {
  width: 100%;
  height: 24px;
  background: var(--gray);
  border-radius: 5px;
  border: 1px solid var(--bg-color);
}

input[type=range]:hover::-webkit-slider-runnable-track,
input[type=range]:focus::-webkit-slider-runnable-track {
  border: 1px solid var(--translucent-white);
}

input[type=range]::-webkit-slider-thumb {
  height: 25px;
  width: 24px;
  border-radius: 2px;
  background: var(--red);
  -webkit-appearance: none;
          appearance: none;
  -webkit-transform: translateY(-5%);
          transform: translateY(-5%);
}

input[type=range]:hover::-webkit-slider-thumb,
input[type=range]:focus::-webkit-slider-thumb {
  background-color: var(--light-red);
}
select, button {
  font-size: 0.7em;
}

button {
  display: inline-block;
  background-color:var(--gray);
  color: var(--white);
  margin: 0.25em;
  padding: 0.8em 0.5em;
  outline:none;
  cursor: pointer;
  border-radius: 3px;
  border: 1px solid var(--bg-color);
  -webkit-transition: all 120ms linear;
  -o-transition: all 120ms linear;
  transition: all 120ms linear;
  overflow: visible;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

button:focus {
  border: 1px solid var(--translucent-white);
}

button:hover {
  background-color: var(--light-gray);
  border: 1px solid var(--translucent-white);
}

button.on{
  background-color:var(--red);
}

button.on:hover {
  background-color:var(--light-red);
}

[data-title]{
  position: relative;
}

[data-title]:after{
  display: block;
  visibility: hidden;
  content: attr(data-title);
  color: var(--white);
  background-color: var(--translucent-bg-color);
  padding: 4px 2px;
  border-radius: 3px;
  border: 1px solid var(--gray);
  position: absolute;
  min-width: 80px;
  bottom: 100%;
  left: 50%;
  -webkit-transform: translate3d(-50%, -2px, 0);
          transform: translate3d(-50%, -2px, 0);
  z-index: 2;
  white-space: pre-line;
  width: 0;
  height: 0;
  opacity: 0;
  -webkit-transition: 160ms all linear;
  -o-transition: 160ms all linear;
  transition: 160ms all linear;
  font-size: 10px !important;
  -webkit-box-shadow: 0px 1px 6px 1px var(--bg-color);
          box-shadow: 0px 1px 6px 1px var(--bg-color);
}

[data-title]:hover:after{
  display: block;
  -webkit-animation: fade-in;
          animation: fade-in;
  -webkit-animation-duration: 120ms;
          animation-duration: 120ms;
  -webkit-animation-delay: 2s;
          animation-delay: 2s;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
  -webkit-transition: 160ms all linear;
  -o-transition: 160ms all linear;
  transition: 160ms all linear;
}

input,
button,
canvas,
textarea,
.keys {
  -webkit-app-region: no-drag;
}

.toolbar {
  width: calc(99.9% * 12/12 - (30px - 30px * 12/12));
  margin: 0;
}

.toolbar:nth-child(1n) {
  float: left;
  margin-right: 30px;
  clear: none;
}

.toolbar:last-child {
  margin-right: 0;
}

.toolbar:nth-child(12n) {
  margin-right: 0;
  float: right;
}

.toolbar:nth-child(12n + 1) {
  clear: both;
}

.toolbar:before {
  content: '';
  display: table;
}

.toolbar:after {
  content: '';
  display: table;
  clear: both;
}

.toolbar label {
  display: block !important;
  width: 100%;
  clear: both;
  margin-top: 10px;
}

.toolbar button {
  width: 29px;
  height: 29px;
  background-repeat: no-repeat;
  background-size: 100%;
  background-position: center center;
}

.logo-holder {
  width: calc(99.9% * 4.5/15 - (30px - 30px * 4.5/15));
  position:relative;
  display: block;
  min-height: 70px;
}

.logo-holder:nth-child(1n) {
  float: left;
  margin-right: 30px;
  clear: none;
}

.logo-holder:last-child {
  margin-right: 0;
}

.logo-holder:nth-child(15n) {
  margin-right: 0;
  float: right;
}

.logo-holder:nth-child(15n + 1) {
  clear: both;
}

.logo-holder:before {
  content: '';
  display: table;
}

.logo-holder:after {
  content: '';
  display: table;
  clear: both;
}

.logo-holder .logo {
  position: absolute;
  top: 3px;
  left: 50%;
  -webkit-transform: translateX(-25.5%);
      -ms-transform: translateX(-25.5%);
          transform: translateX(-25.5%);
  display: block;
  height: 100px;
  width: 112px;
  font-family: "PT Sans";
  background-image: url("./images/quirki_m3_logo.svg");
  background-size: contain;
  background-repeat: no-repeat;
  z-index: 3;
}


